body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*
 * credit tohttps://codepen.io/osorina/pen/PQdMOO
 */

* {
  margin: 0; 
  padding: 0;
  box-sizing: border-box !important;
}

.monospace {
  color: #CFCFCF;
  font-family: "Roboto Mono", monospace, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 100%; /* 18px */
  letter-spacing: -0.45px;
  position: absolute;
  left: 7vw;
  margin-top: calc(22vh + 300px);
}

.btnContainer {
  margin-top: 85vh;
  position: absolute;
  width: 100%;
}

.name {
  margin-top: 22vh;
  margin-left: 5vw;
  position: absolute;
  font-family: "Lalezar", sans-serif;
  font-size: 196px;
  font-style: normal;
  font-weight: 400;
  line-height: 80%;

  background: linear-gradient(100deg, #f1f1f1 10%, #8b8b8b 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.shadow {
  margin-top: 22vh;
  margin-left: 5vw;
  position: absolute;
  font-family: "Lalezar", sans-serif;
  font-size: 196px;
  font-style: normal;
  font-weight: 400;
  line-height: 80%;
  text-shadow: 12px 8px 4px rgb(0, 0, 0);
}

html, body {
  height: 100%;
}

body {
  display: table;
  width: 100%;
  height: 100%;
  /* background-color: #292929; */
  background: linear-gradient(180deg, #303030 0%, #0D0D0D 100%);
  color: #000;
  line-height: 1.6;
  position: relative;
  font-family: "Lalezar", sans-serif;
  overflow: hidden;
}









